.ToDoItemLoading {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 72px;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.ToDoItemLoading-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.ToDoItemLoading-check,
.ToDoItemLoading-delete {
  border-radius: 50px;
  height: 48px;
  width: 48px;
}

.ToDoItemLoading-check {
  position: absolute;
  left: 12px;
}

.ToDoItemLoading-delete {
  position: absolute;
  right: 6px;
}

.ToDoItemLoading,
.ToDoItemLoading-check,
.ToDoItemLoading-delete {
  background: linear-gradient(90deg, rgb(250, 250, 250), rgb(200, 199, 199));
  background-size: 400% 400%;
  animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
